<template>
    <div class="top_all">
        <div class="header">
            <div class='back' @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="but-all">
                <div
                    v-for="(item,index) in all"
                    :key='index'
                    @click='go(index)'
                    :class='{text_hit:true , text_hit_change:isChange == index}'>
                    <span style="line-height:0.61rem;">{{item.title}}</span>
                </div>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
  name: 'text_list_top',
  data () {
    return {
      isChange: 0,
      all: [
        { title: '待考', path: '/text_list_wait' },
        { title: '已考', path: '/text_list' }
      ],
      fromText: 0 // 是否是从考试进入
    }
  },
  created () {
    this.fromText = this.$route.params.fromText
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  methods: {
    // 点击待考列表和已考列表
    go (index) {
      this.isChange = index
      this.$router.replace(this.all[index].path)
    },
    // 返回
    back () {
      if (this.fromText === 1) {
        this.$router.replace('/set')
      } else {
        this.$router.back()
      }
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    }
  }
}
</script>
<style scoped>
    .top_all{
        width: 100%;
        height: 100%;
        background: #fff;
    }
   .header{
        width:100%;
        background:#fff;
        font-size:0.34rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .header .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .header .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .but-all{
        width: 3rem;
        height: 0.58rem;
        border-radius:0.29rem;
        font-size:0.28rem;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:#5794F6;
        border:0.012rem solid #5794F6;
        display: flex;
        box-sizing:border-box;
    }
    .text_hit{
        width: 1.5rem;
        height: 100%;

        /* padding-top:0.023rem; */
        /* line-height:0.6rem; */
        /* display: flex; */
        /* align-items: center; */
        /* justify-content: center;  */

    }
    .text_hit_change{
        background:linear-gradient(180deg,rgba(82,203,151,1) 0%,rgba(133,225,166,1) 100%);
        border-radius:0.29rem;
        color: #fff;

    }
</style>



